*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --fontFamily: Roboto, sans-serif;
  --accentBtnColor: #ff6b02;
  --accentTextColor: #ff6b08;
  --notificationTextColor: #ff001b;
  --primaryTextDark: #000;
  --primaryTextLight: #fff;
  --secondaryTextColor: #545454;
  --secondaryTextDetailsColor: #8c8c8c;
  --primaryLightThemeBgColor: #fff;
  --secondaryLightThemeBgColor: #f7f7f7;
  --headerGradientColor: rgba(0, 0, 0, .56);
  --secondDarkBgColor: #0f0f0f;
  --timingFunction: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  font-family: var(--fontFamily);
  color: var(--primaryTextDark);
  background-color: var(--primaryLightThemeBgColor);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  padding: 0;
  font-family: inherit;
}

.container {
  max-width: 100%;
  height: auto;
  margin: 20px auto 40px;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    margin: 60px auto;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding: 0 16px;
  }
}

.content {
  flex: 1;
}

.modal-auth {
  min-width: min-content;
  max-width: 360px;
  transition: transform var(--timingFunction);
  background-color: var(--primaryLightThemeBgColor);
  border-radius: 8px;
  padding: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

@media screen and (min-width: 768px) {
  .modal-auth {
    max-width: 450px;
    min-width: 450px;
    width: 500px;
    height: 520px;
  }
}

.modal-auth__container {
  width: 240px;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .modal-auth__container {
    width: 336px;
  }
}

.modal-auth__box-line {
  position: relative;
}

.modal-auth__box-line:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: var(--notificationTextColor);
  position: absolute;
  bottom: -8px;
  left: 0;
}

.modal-auth__list {
  justify-content: space-around;
  display: flex;
}

.modal-auth__btn {
  font-family: var(--fontFamily);
  color: var(--primaryTextDark);
  text-align: center;
  width: 100%;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
  display: flex;
}

.auth-form {
  margin-top: 46px;
}

.auth-form__input {
  width: 100%;
  border: 1px solid var(--secondaryTextColor);
  border-radius: 5px;
  margin-bottom: 25px;
  padding: 11px 12px;
}

@media screen and (min-width: 768px) {
  .auth-form__input {
    width: 100%;
  }
}

.auth-form__input:focus {
  outline: 2px solid var(--accentTextColor);
  border: none;
}

.auth-form__input-email::-ms-input-placeholder, .auth-form__input-password::-ms-input-placeholder {
  color: var(---secondaryTextDetailsColor);
}

.auth-form__input-email::placeholder, .auth-form__input-password::placeholder {
  color: var(---secondaryTextDetailsColor);
}

.auth-form__input-password {
  position: relative;
}

.icon-eye {
  width: 20px;
  height: 20px;
  display: flex;
  position: absolute;
  top: 42.5%;
  right: 15%;
  transform: translateY(50%);
}

@media screen and (min-width: 768px) {
  .icon-eye {
    top: 33.5%;
    right: 15%;
    transform: translateY(50%);
  }
}

.icon-eye.show-pswd, .icon-eye.show-pswd, .icon-eye.show-pswd {
  fill: var(--primaryTextDark);
}

.icon-eye.hide-pswd, .icon-eye.hide-pswd, .icon-eye.hide-pswd {
  fill: var(--secondaryTextDetailsColor);
}

.hidden-tab {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.auth-form__text {
  text-align: center;
  font-family: var(--fontFamily);
  color: var(--primaryTextDark);
  margin-bottom: 10px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.6;
}

.auth-form__sing-in-text {
  color: var(--primaryTextDark);
  font-family: var(--fontFamily);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.6;
}

.auth-form__button {
  width: 100%;
  background-color: var(--accentTextColor);
  color: var(---primaryTextLight);
  color: var(--primaryTextLight);
  transition: transform var(--timingFunction);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin-bottom: 28px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.1;
  display: flex;
  transform: scale(1);
  box-shadow: 0 19px 38px rgba(0, 0, 0, .3), 0 15px 12px rgba(0, 0, 0, .22);
}

.auth-form__button:hover, .auth-form__button:focus {
  transform: scale(1.05);
}

.auth-form__autorization {
  justify-content: center;
  align-items: center;
  display: flex;
}

.auth-form__button-google {
  width: 100%;
  transition: transform var(--timingFunction);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin-bottom: 28px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.1;
  display: flex;
  transform: scale(1);
  box-shadow: 0 19px 38px rgba(0, 0, 0, .3), 0 15px 12px rgba(0, 0, 0, .22);
}

.auth-form__button-google:hover, .auth-form__button-google:focus {
  border-color: var(--accentTextColor);
  transform: scale(1.05);
}

.auth-form__link {
  font-family: var(--fontFamily);
  color: var(--accentTextColor);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.67;
}

.auth-form__link:hover, .auth-form__link:focus {
  color: var(--primaryTextDark);
}

.modal-auth__btn-close {
  width: 30px;
  height: 30px;
  background-color: var(--color-white);
  cursor: pointer;
  transition: fill var(--transition);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 16px;
  right: 16px;
}

.modal-auth__btn-close:hover, .modal-auth__btn-close:focus {
  fill: var(--accentTextColor);
  border-color: var(--accentTextColor);
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 40;
  visibility: hidden;
  pointer-events: none;
  opacity: 0;
  transition: all var(--transition);
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
}

.backdrop.active {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
}

.backdrop .modal {
  transform: translateX(-50%)scale(1);
}

@media screen and (min-width: 1280px) {
  .backdrop .modal {
    transform: translate(-50%, -50%)scale(1);
  }
}

.modal1.active, .modal2.active, .modal3.active {
  visibility: visible;
  opacity: 1;
  pointer-events: initial;
}

.spinner {
  z-index: 11;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(105, 1, 1, .4), rgba(234, 229, 221, .4));
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: fixed;
  top: 0;
  left: 0;
}

@media screen and (min-width: 768px) {
  .spinner {
    background-position: 54% 0;
  }
}

@media screen and (min-width: 1280px) {
  .spinner {
    background-position: 0 10%;
  }
}

.spinner__overlay {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 768px) {
  .spinner__overlay {
    transform: translate(-40%, -50%);
  }
}

@media screen and (min-width: 1280px) {
  .spinner__overlay {
    transform: translate(-40%, -10%);
  }
}

.spinner__pic {
  animation: spinner 1.2s linear infinite;
}

@keyframes spinner {
  0% {
    transform: scale(.5);
  }

  100% {
    transform: scale(1);
  }
}

.spinner__text {
  text-align: center;
  letter-spacing: .03em;
  margin-top: 20px;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.25;
}

@media screen and (min-width: 768px) {
  .spinner__text {
    letter-spacing: .03em;
    font-size: 30px;
    font-weight: 700;
    line-height: 1.16667;
  }
}

@media screen and (min-width: 1280px) {
  .spinner__text {
    letter-spacing: .03em;
    font-size: 30px;
    font-weight: 700;
    line-height: 1.16667;
  }
}

@keyframes spinner-line-fade-more {
  0%, 100% {
    opacity: 0;
  }

  1% {
    opacity: 1;
  }
}

@keyframes spinner-line-fade-quick {
  0%, 39%, 100% {
    opacity: .25;
  }

  40% {
    opacity: 1;
  }
}

@keyframes spinner-line-fade-default {
  0%, 100% {
    opacity: .22;
  }

  1% {
    opacity: 1;
  }
}

@keyframes spinner-line-shrink {
  0%, 25%, 100% {
    opacity: .25;
    transform: scale(.5);
  }

  26% {
    opacity: 1;
    transform: scale(1);
  }
}

.spinner-backdrop {
  width: 100%;
  height: 100%;
  background-color: #af2c00;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.is-closed {
  display: none;
}

.team__modal--close {
  fill: #373737;
  background: none;
  text-decoration: none;
  transition-property: transform;
  transition-duration: .5s;
  position: absolute;
  top: 10px;
  right: 10px;
}

.team__modal--close:hover, .team__modal--close:focus {
  fill: var(--accentTextColor);
  transform: scale(1.4);
}

@media screen and (min-width: 768px) {
  .team__modal--close {
    top: 15px;
    right: 15px;
  }

  .team__modal--close:hover, .team__modal--close:focus {
    top: 15px;
    right: 15px;
    transform: scale(1.8);
  }
}

@media screen and (min-width: 1280px) {
  .team__modal--close {
    top: 20px;
    right: 20px;
  }

  .team__modal--close:hover, .team__modal--close:focus {
    transform: scale(2);
  }
}

.team__modal {
  min-width: 300px;
  max-width: 430px;
  height: 550px;
  transition: transform var(--timingFunction);
  background-color: #fff;
  border-radius: 5px;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 7px;
  display: flex;
  position: absolute;
  top: 30px;
  left: 50%;
}

@media screen and (min-width: 768px) {
  .team__modal {
    width: 600px;
    max-width: none;
    height: 600px;
    flex-direction: column;
    align-items: center;
    top: 10%;
    left: 50%;
  }
}

@media screen and (min-width: 1280px) {
  .team__modal {
    width: 800px;
    max-width: none;
    height: 690px;
    flex-direction: column;
    align-items: center;
    top: 50%;
    left: 50%;
  }
}

.team__title {
  color: var(--accentTextColor);
  margin-top: 13px;
}

@media screen and (min-width: 768px) {
  .team__title {
    margin-top: 0;
    margin-bottom: 30px;
  }
}

.team__title .team__shadow {
  box-shadow: 10px 1px 800px 70px rgba(255, 85, 0, .7), 1px 2px 4px rgba(255, 85, 0, .7), 2px 4px 8px rgba(255, 85, 0, .5), 2px 4px 16px rgba(255, 85, 0, .5);
}

@media screen and (min-width: 1280px) {
  .team__title {
    margin-top: 0;
    margin-bottom: 45px;
    font-size: 35px;
  }
}

.team__wrapper {
  width: 278px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .team__wrapper {
    width: 490px;
    gap: 25px;
    margin-bottom: 35px;
  }
}

@media screen and (min-width: 1280px) {
  .team__wrapper {
    width: 690px;
    gap: 45px;
    margin-bottom: 35px;
  }
}

.team__lead {
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .team__lead {
    gap: 25px;
  }
}

@media screen and (min-width: 1280px) {
  .team__lead {
    gap: 45px;
  }
}

.team__member {
  transition: transform var(--timingFunction);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.team__member:hover, .team__member:focus {
  transform: scale(1.15);
}

@media screen and (min-width: 768px) {
  .team__member:hover, .team__member:focus {
    transform: scale(1.3);
  }
}

@media screen and (min-width: 1280px) {
  .team__member:hover, .team__member:focus {
    transform: scale(1.45);
  }
}

.team__photo {
  height: 80px;
  border-radius: 50%;
  box-shadow: 2px 2px 10px 5px #49940c;
}

@media screen and (min-width: 768px) {
  .team__photo {
    height: 100px;
  }
}

@media screen and (min-width: 1280px) {
  .team__photo {
    height: 120px;
  }
}

.team__github {
  fill: #a6a6a6;
  margin-top: 3px;
  animation: spin 5s infinite;
}

.team__github:hover, .team__github:focus {
  fill: var(--accentTextColor);
}

@media screen and (min-width: 768px) {
  .team__github {
    width: 30px;
    height: 30px;
    margin-top: 4px;
  }
}

@media screen and (min-width: 1280px) {
  .team__github {
    width: 35px;
    height: 35px;
    margin-top: 6px;
  }
}

@keyframes spin {
  0% {
    transform: rotateY(0deg);
  }

  100% {
    transform: rotateY(360deg);
  }
}

.team__name {
  color: #252525;
  font-size: 18px;
  font-weight: 600;
}

@media screen and (min-width: 768px) {
  .team__name {
    font-size: 19px;
  }
}

@media screen and (min-width: 1280px) {
  .team__name {
    font-size: 22px;
  }
}

.team__position {
  color: #373737;
  font-size: 15px;
  font-weight: 500;
}

@media screen and (min-width: 1280px) {
  .team__position {
    font-size: 18px;
  }
}

.team__circle {
  z-index: 0;
  filter: contrast(1.3);
  background: none;
  position: relative;
}

.team__circle:before {
  content: "";
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  z-index: 0;
  border: 3px solid #84d120;
  border-radius: 50%;
  transition: border-color .5s;
  position: absolute;
  top: 0;
  bottom: 40px;
  left: 0;
  right: 40px;
}

.team__circle:hover {
  cursor: pointer;
}

.team__circle:hover:before {
  animation: main-color 5s ease-in-out infinite alternate;
}

.team__circle:hover:after {
  animation: highlight-color 5s ease-in-out infinite alternate;
}

.animated {
  animation: bgPos 20s ease-in-out infinite alternate;
}

@keyframes bgPos {
  from {
    background-position: 0 0;
  }

  to {
    background-position: 512px 512px;
  }
}

@keyframes main-color {
  from {
    border-color: #00b7ff;
  }

  40% {
    border-color: #ffd500;
  }

  to {
    border-color: #c20000;
  }
}

@keyframes highlight-color {
  from {
    border-color: rgba(199, 248, 255, .6);
  }

  60% {
    border-color: rgba(227, 199, 255, .6);
  }

  to {
    border-color: rgba(255, 232, 20, .643);
  }
}

.position {
  justify-content: center;
  align-items: center;
  padding-top: 60px;
  padding-bottom: 60px;
  display: flex;
}

.pagination ul {
  border-radius: 50px;
  padding: 8px;
  display: flex;
}

.pagination ul li {
  color: var(--primaryTextDark);
  text-align: center;
  cursor: pointer;
  transition: var(--timingFunction);
  font-size: 12px;
  font-weight: 500;
  line-height: 45px;
}

.pagination ul li.numb {
  height: 40px;
  width: 40px;
  border-radius: 5px;
  margin: 0 3px;
}

.pagination ul li.dots {
  cursor: default;
  font-size: 22px;
}

.pagination ul li.btn {
  border-radius: 50px;
  padding: 0 20px;
}

.pagination ul li.prev {
  border-radius: 25px 5px 5px 25px;
}

.pagination ul li.next {
  border-radius: 5px 25px 25px 5px;
}

.pagination ul li.active, .pagination ul li.numb:hover, .pagination ul li.btn:hover {
  color: var(--primaryTextLight);
  background: var(--accentTextColor);
}

.film-list {
  flex-direction: column;
  align-items: center;
  gap: 15px;
  padding-bottom: 20px;
  list-style: none;
  display: flex;
}

.film-card {
  cursor: pointer;
  max-width: 280px;
  height: 470px;
  border-radius: 5px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  overflow: hidden;
  box-shadow: 0 3px 6px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .23);
}

.film-card:hover, .film-card:focus {
  transform: scale(1.03);
  box-shadow: 6px 2px 16px rgba(136, 165, 191, .48), -6px -2px 16px rgba(255, 255, 255, .8);
}

.film-card__img {
  width: 280px;
  height: 402px;
}

.card-wrap {
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
}

.film-card__title {
  color: #000;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

.film-card__info {
  color: #ff6b08;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

@media screen and (min-width: 768px) {
  .film-list {
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
    gap: 32px;
  }

  .film-card__img {
    width: 336px;
    height: 455px;
  }

  .film-card {
    max-width: 336px;
    height: 525px;
